@import 'variables.scss';
@import 'mixins.scss';


.about-header {
	display: flex;
	justify-content: center;
	padding: 20px 0 30px 0;

	hr {
		width: 15%;
		border-top: 1px solid;
		color: #000;
		background-color: #000;
		display: inline-block;
		margin-left: 0;
		margin-right: 0;
		margin-top: 12px;
	}

	h2 {
		display: inline-block;
		margin: 0 1em;
		font-family: 'Cinzel', sans-serif;
		font-weight: 300;
		font-size: 1.3em;
		color: #000;
		letter-spacing: 5px;

	}
}


.about-content {
	height: 1000px;
	color: #fff;

	.about-philosophy {
		background-color: $contentOrange;
		width: 60%;
		height: 100%;
		float: left;
		padding: 3em;

	}

	img {
		float: left;
		margin: 5px 15px 0 15px;
	}

	p {

	}

	h3 {
		font-family: 'Cinzel', sans-serif;
		font-size: 1em;
		font-weight: 300;
		text-align: center;
		letter-spacing: 5px;
		color: #fff;
		padding-bottom: 20px;
	}


	/* Dropdown Button */
	.dropbtn {
	    background-color: #7799ad;
	    color: white;
	    padding: 16px;
	    width: 600px;
	    font-family: 'Source Sans Pro', sans-serif;
	    font-weight: 300;
	    font-size: 16px;
	    border: none;
	    cursor: pointer;
	}

	/* Dropdown button on hover & focus */
	.dropbtn:hover, .dropbtn:focus {
	    background-color: #8db0c6;
	}

	/* The container <div> - needed to position the dropdown content */
	.dropdown {
			padding-top: 40px;
	    position: relative;
	    display: inline-block;
	}

	/* Dropdown Content (Hidden by Default) */
	.dropdown-content {
	    display: none;
	    position: absolute;
	    background-color: #f9f9f9;
	    width: 100%;
	    overflow: auto;
	    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
	}

	/* Links inside the dropdown */
	.dropdown-content p {
	    color: black;
	    padding: 5px 16px;
	    font-family: 'Source Sans Pro', sans-serif;
	    font-weight: 300;
	    font-size: 16px;
	    text-decoration: none;
	    display: block;
	}

	/* Change color of dropdown links on hover */
	// .dropdown-content a:hover {background-color: #f1f1f1}

	/* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
	.show {display:block;}


