.home-intro {
	h2 {
		text-align: center;
		font-family: 'Cinzel', sans-serif;
		font-weight: 300;
		font-size: 1.3em;
		letter-spacing: 5px;
		padding: 0.7em 0 0.9em 0;
	}

	p {
		font-family: 'Hind', sans-serif;
		font-weight: 300;
		font-size: 0.9em;
		letter-spacing: 1px;
		line-height: 22px;
		padding-bottom: 35px;
	}
}


.orange-content-wrapper {
	background-color: $contentOrange;

	.primary-practice-areas {
		display: flex;
		justify-content: center;
		padding-top: 0.7em;

			hr {
			width: 15%;
			border-top: 1px;
			color: #fff;
			background-color: #fff;
			display: inline-block;
			margin-left: 0;
			margin-right: 0;
			margin-top: 29px;
			}

			h2 {
				display: inline-block;
				margin: 0 1em;
				font-family: 'Cinzel', sans-serif;
				font-weight: 300;
				font-size: 1.3em;
				color: #fff;
				letter-spacing: 5px;
				padding: 0.7em 0;
			}
		}

	.mediation-feature {
		padding: 20px;

			h3 {
				font-family: 'Cinzel', sans-serif;
				font-weight: 300;
				font-size: 1em;
				text-align: center;
				letter-spacing: 2px;
				color: #fff;
			}

			h3 > a {
				color: #fff;
			}

			img {
				display: block;
				margin: 0 auto;
			}

			p {
				font-family: 'Hind', sans-serif;
				font-weight: 300;
				font-size: 0.9em;
				color: #fff;
				letter-spacing: 1px;
				line-height: 22px;
				text-align: justify;
			}
		}

		.support-feature {
			padding: 20px;

			h3 {
				font-family: 'Cinzel', sans-serif;
				font-weight: 300;
				font-size: 1em;
				text-align: center;
				letter-spacing: 2px;
				color: #fff;
			}

			h3 > a {
				color: #fff;
			}

			img {
				display: block;
				margin: 0 auto;
			}

			p {
				font-family: 'Hind', sans-serif;
				font-weight: 300;
				font-size: 0.9em;
				color: #fff;
				letter-spacing: 1px;
				line-height: 22px;
				text-align: justify;
			}
		}

		a {
			// text-decoration: underline;
			color: #0066a5;
		}

		a:hover {
			color: #fff;
		}










}

















