@import 'variables.scss';
@import 'mixins.scss';


@import 'variables.scss';
@import 'mixins.scss';

.self-help-container {

}

.orange-content-wrapper {
	background-color: $contentOrange;

	img {
		margin-top: 50px;
	}

	.self-help-intro {
		min-height: 410px;
		margin-top: 50px;

		h3 {
			font-family: 'Cinzel', sans-serif;
			font-size: 1em;
			font-weight: 300;
			text-align: center;
			letter-spacing: 4px;
			color: #fff;
			padding-bottom: 20px;
		}

		.disclaimer {
			font-family: 'Hind', sans-serif;
			font-size: 0.8em;
			letter-spacing: 1px;
			color: #fff;
			font-style: italic;
			padding-top: 30px;
		}

		.disclaimer-bold {
			font-weight: 500;
		}
	}
}

.blue-selfhelp-content-wrapper {
	background-color: $navBlue;
	color: #fff;
	min-height: 550px;

	.self-help-feature {

		a {
			color: #000;

			&:hover {
				color: #fff;
			}
		}


	ul {
		font-family: 'Hind', sans-serif;


		li {
			color: $linkHoverOrange;
			}
		}
	}



}