@import 'variables.scss';
@import 'mixins.scss';

header {
	background-color: $navBlue;
	color: #ffffff;

	section {
		display: flex;
		flex-direction: row;
	}

	h1 {
		font-family: 'Cinzel', serif;
		font-size: 1.9em;
		font-weight: 300;
		letter-spacing: 2px;
		width: 350px;
		bottom:94px;
		left: 100px;
		position:relative;
		color: #fff;
	}

	.small-logo-text {
		font-size: 0.7em;
		padding-left: 25px;
	}

	br {
		margin-top: 100px;
	}
	a {
		text-decoration: none;
	}

	a:visited {
		color: #fff;
	}



	.col-1-3 {
		height: 120px;
	}

	.contact {
		float:right;
	}

	p {
		text-align: right;
		font-family: 'Hind', sans-serif;
		font-weight: 400;
		font-size: 0.9em;
		letter-spacing: 0.9px;
		text-transform: none;
		line-height: 18px;

			a {
				color: $linkHoverOrange;

				&:hover {
					color: #fff;
				}
			}


	}
}

nav {
	padding-bottom: 1px;

	ul {
		display: flex;
		flex-direction: row;
		justify-content: center;
		list-style-type: none;
	}
	a {
		color: $linkWhite;
		text-align: center;
		padding: 0px 70px 0px 60px;
		font-family: 'Hind', sans-serif;
		font-weight: 300;
		font-size: 0.9em;
		letter-spacing: 0.8px;
		text-transform: uppercase;

		&:hover {
			color: $linkHoverOrange;
		}
	}
}


